<template>
    <v-container>
        <v-data-table
            :headers="headers"
            :items="invitations"
            multi-sort
            loading-text="..."
            :loading="loading"
            class="elevation-1"
        >
            <template v-slot:top>
                <v-toolbar flat color="white">
                    <v-toolbar-title></v-toolbar-title>
                    <v-divider class="mx-4" inset vertical></v-divider>
                    <v-spacer></v-spacer>
                    <v-dialog
                        v-model="dialog"
                        :fullscreen="!$vuetify.breakpoint.lgAndUp"
                        max-width="500px"
                    >
                        <template v-slot:activator="{ on }">
                            <v-btn color="accent" v-on="on"></v-btn>
                        </template>
                        <v-card>
                            <v-card-title>
                                <span class="headline">{{ formTitle }}</span>
                            </v-card-title>
                            <v-card-text>
                                <v-container>
                                    <v-row>
                                        <v-col cols="12">
                                            <v-text-field v-model="editedItem.title" label=""></v-text-field>
                                        </v-col>
                                        <v-col cols="12">
                                            <v-textarea
                                                v-model="editedItem.text"
                                                label=""
                                                no-resize
                                            ></v-textarea>
                                        </v-col>
                                        <v-col cols="12">
                                            <!--  -->
                                            <v-file-input accept="image/*" multiple label=""></v-file-input>
                                        </v-col>
                                        <v-col cols="12">
                                            <v-menu
                                                ref="menu"
                                                v-model="menu"
                                                :close-on-content-click="false"
                                                :return-value.sync="editedItem.deadline_at"
                                                transition="scale-transition"
                                                offset-y
                                                min-width="290px"
                                            >
                                                <template v-slot:activator="{ on }">
                                                    <v-text-field
                                                        v-model="editedItem.deadline_at"
                                                        label=""
                                                        readonly
                                                        v-on="on"
                                                    ></v-text-field>
                                                </template>
                                                <v-date-picker
                                                    v-model="editedItem.deadline_at"
                                                    no-title
                                                    scrollable
                                                >
                                                    <v-spacer></v-spacer>
                                                    <v-btn
                                                        text
                                                        color="primary"
                                                        @click="menu = false"
                                                    ></v-btn>
                                                    <v-btn
                                                        text
                                                        color="primary"
                                                        @click="$refs.menu.save(editedItem.deadline_at)"
                                                    ></v-btn>
                                                </v-date-picker>
                                            </v-menu>
                                        </v-col>
                                        <v-col cols="12">
                                            <v-select-members
                                                v-model="editedItem.attend_members"
                                                return-object
                                            ></v-select-members>
                                        </v-col>
                                    </v-row>
                                </v-container>
                            </v-card-text>

                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="blue darken-1" text @click="close"></v-btn>
                                <v-btn color="blue darken-1" text @click="save"></v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-dialog>
                </v-toolbar>
            </template>
            <template v-slot:item.title="{ item }">
                <span class="d-inline-block text-truncate" style="max-width:200px;">{{item.title}}</span>
            </template>
            <template v-slot:item.text="{ item }">
                <span class="d-inline-block text-truncate" style="max-width:200px;">{{item.text}}</span>
            </template>
            <template
                v-slot:item.deadline_at="{ item }"
            >{{item.deadline_at | date_format("yyyyMMdd")}}</template>
            <template v-slot:item.created_at="{ item }">{{item.created_at | date_format}}</template>
            <template
                v-slot:item.attend_member_count="{ item }"
            >{{item.attend_members && item.attend_members.length}}</template>
            <template v-slot:item.action="{ item }">
                <v-icon small class="mr-2" @click="editItem(item)">mdi-pencil</v-icon>
                <v-icon small @click="deleteItem(item)">mdi-delete</v-icon>
            </template>
            <template v-slot:no-data></template>)
        </v-data-table>
    </v-container>
</template>

<script>
import store from "../../store";
export default {
    data: () => ({
        loading: true,
        dialog: false,
        menu: false, //data-picker
        headers: [
            {
                text: "",
                align: "left",
                sortable: false,
                value: "title"
            },
            {
                text: "",
                align: "left",
                sortable: false,
                value: "text"
            },
            {
                text: "",
                align: "left",
                sortable: false,
                value: "attend_member_count"
            },
            {
                text: "",
                align: "right",
                sortable: true,
                value: "deadline_at"
            },
            {
                text: "",
                align: "right",
                sortable: true,
                value: "created_at"
            },
            { text: "", align: "right", sortable: false, value: "action" }
        ],
        invitations_collection: store.collection("invitations"),
        unsubscribe: null,
        invitations: [],
        editedIndex: -1,
        editedItem: {
            title: "",
            text: "",
            images: [],
            attend_members: [],
            deadline_at: ""
        },
        defaultItem: {
            title: "",
            text: "",
            images: [],
            attend_members: [],
            deadline_at: ""
        }
    }),
    created() {
        this.invitations_collection.get().then(snapshot => {
            this.setData(snapshot);
            this.loading = false;
        });
        this.unsubscribe = this.invitations_collection.onSnapshot(this.setData);
    },
    computed: {
        formTitle() {
            return this.editedIndex === -1
                ? ""
                : "";
        }
    },

    watch: {
        dialog(val) {
            val || this.close();
        }
    },
    methods: {
        setData(snapshot) {
            this.invitations = snapshot.docs.map(doc => ({
                ...doc.data(),
                _id: doc.id
            }));
        },
        editItem(item) {
            this.editedIndex = this.invitations.indexOf(item);
            this.editedItem = Object.assign({}, item);
            this.dialog = true;
        },

        deleteItem(item) {
            confirm("") &&
                this.invitations_collection.doc(item._id).delete();
        },

        close() {
            this.dialog = false;
            setTimeout(() => {
                this.editedItem = Object.assign({}, this.defaultItem);
                this.editedIndex = -1;
            }, 300);
        },

        save() {
            if (this.editedIndex > -1) {
                this.invitations_collection.doc(this.editedItem._id).set({
                    ...this.editedItem,
                    deadline_at: new Date(this.editedItem.deadline_at)
                });
            } else {
                this.invitations_collection.add({
                    ...this.editedItem,
                    created_at: new Date(),
                    deadline_at: new Date(this.editedItem.deadline_at)
                });
            }
            this.close();
        }
    },
    destroyed() {
        this.unsubscribe();
    }
};
</script>

