<template>
    <v-container>
        <v-data-table
            :headers="headers"
            :items="forums"
            multi-sort
            loading-text="..."
            :loading="loading"
            class="elevation-1"
        >
            <template v-slot:top>
                <v-toolbar flat color="white">
                    <v-toolbar-title></v-toolbar-title>
                    <v-divider class="mx-4" inset vertical></v-divider>
                    <v-spacer></v-spacer>
                    <v-dialog
                        v-model="dialog"
                        :fullscreen="!$vuetify.breakpoint.lgAndUp"
                        max-width="500px"
                    >
                        <v-card>
                            <v-card-title>
                                <span class="headline">{{ formTitle }}</span>
                            </v-card-title>
                            <v-card-text>
                                <v-container>
                                    <v-row>
                                        <v-col cols="12">
                                            <v-text-field v-model="editedItem.title" label=""></v-text-field>
                                        </v-col>
                                        <v-col cols="12">
                                            <v-textarea
                                                v-model="editedItem.text"
                                                label=""
                                                no-resize
                                            ></v-textarea>
                                        </v-col>
                                        <v-col cols="12">
                                            <!--  -->
                                            <v-file-input accept="image/*" multiple label=""></v-file-input>
                                        </v-col>
                                    </v-row>
                                </v-container>
                            </v-card-text>
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="blue darken-1" text @click="close"></v-btn>
                                <v-btn color="blue darken-1" text @click="save"></v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-dialog>
                </v-toolbar>
            </template>
            <template v-slot:item.title="{ item }">
                <span class="d-inline-block text-truncate" style="max-width:200px;">{{item.title}}</span>
            </template>
            <template v-slot:item.text="{ item }">
                <span class="d-inline-block text-truncate" style="max-width:200px;">{{item.text}}</span>
            </template>
            <template v-slot:item.created_at="{ item }">{{item.created_at | date_format}}</template>
            <template v-slot:item.action="{ item }">
                <v-icon small class="mr-2" @click="editItem(item)">mdi-pencil</v-icon>
                <v-icon small @click="deleteItem(item)">mdi-delete</v-icon>
            </template>
            <template v-slot:no-data></template>
        </v-data-table>
    </v-container>
</template>

<script>
import store from "../../store";
export default {
    data: () => ({
        loading: true,
        dialog: false,
        headers: [
            {
                text: "",
                align: "left",
                sortable: false,
                value: "sender_name"
            },
            {
                text: "",
                align: "left",
                sortable: false,
                value: "title"
            },
            {
                text: "",
                align: "left",
                sortable: false,
                value: "text"
            },
            {
                text: "",
                align: "right",
                sortable: true,
                value: "created_at"
            },
            { text: "", align: "right", sortable: false, value: "action" }
        ],
        forums_collection: store.collection("forums"),
        unsubscribe: null,
        forums: [],
        editedIndex: -1,
        editedItem: {
            title: "",
            text: "",
            images: [],
            comments: []
        },
        defaultItem: {
            title: "",
            text: "",
            images: [],
            comments: []
        }
    }),
    created() {
        this.forums_collection.get().then(snapshot => {
            this.setData(snapshot);
            this.loading = false;
        });
        this.unsubscribe = this.forums_collection.onSnapshot(this.setData);
    },
    computed: {
        formTitle() {
            return "";
        }
    },
    watch: {
        dialog(val) {
            val || this.close();
        }
    },
    methods: {
        setData(snapshot) {
            this.forums = snapshot.docs.map(doc => ({
                ...doc.data(),
                _id: doc.id
            }));
        },
        editItem(item) {
            this.editedIndex = this.forums.indexOf(item);
            this.editedItem = Object.assign({}, item);
            this.dialog = true;
        },

        deleteItem(item) {
            confirm("") &&
                this.forums_collection.doc(item._id).delete();
        },

        close() {
            this.dialog = false;
            setTimeout(() => {
                this.editedItem = Object.assign({}, this.defaultItem);
                this.editedIndex = -1;
            }, 300);
        },

        save() {
            this.forums_collection
                .doc(this.editedItem._id)
                .set(this.editedItem);
            this.close();
        }
    },
    destroyed() {
        this.unsubscribe();
    }
};
</script>

