<template>
    <v-container>
        <v-card class="elevation-1">
            <v-card-title>
                
                <v-spacer></v-spacer>
                <v-text-field
                    v-model="search"
                    append-icon="mdi-magnify"
                    label="..."
                    single-line
                    hide-details
                ></v-text-field>
            </v-card-title>
            <v-data-table
                :headers="headers"
                :items="members"
                :search="search"
                multi-sort
                loading-text="..."
                :loading="loading"
            >
                <template v-slot:item.profile_img="{item}">
                    <v-avatar color="grey" size="40">
                        <img :src="item.profile_img" />
                    </v-avatar>
                </template>
            </v-data-table>
        </v-card>
    </v-container>
</template>

<script>
import store from "../../store";
export default {
    data: () => ({
        search: "",
        loading: true,
        headers: [
            {
                text: "",
                align: "center",
                sortable: false,
                value: "profile_img"
            },
            {
                text: "",
                align: "left",
                sortable: true,
                value: "name"
            },
            {
                text: "",
                align: "left",
                sortable: true,
                value: "ruby"
            },
            {
                text: "",
                align: "left",
                sortable: true,
                value: "company_name"
            },
            {
                text: "",
                align: "left",
                sortable: true,
                value: "post"
            },
            {
                text: "",
                align: "left",
                sortable: true,
                value: "department_name"
            }
        ],
        members_collection: store.collection("members"),
        unsubscribe: null,
        members: []
    }),
    created() {
        this.members_collection.get().then(snapshot => {
            this.setData(snapshot);
            this.loading = false;
        });
        this.unsubscribe = this.members_collection.onSnapshot(this.setData);
    },
    methods: {
        setData(snapshot) {
            this.members = snapshot.docs.map(doc => ({
                ...doc.data(),
                _id: doc.id
            }));
        }
    },
    destroyed() {
        this.unsubscribe();
    }
};
</script>

