<template>
    <v-select
        :label="label"
        v-model="selected"
        :items="companies"
        item-text="name"
        item-value="_id"
        :loading="loading"
    ></v-select>
</template>

<script>
import store from "../store";
export default {
    props: {
        label: String,
        value: String
    },
    data: () => ({
        loading: true,
        headers: [
            {
                text: "",
                align: "left",
                sortable: true,
                value: "name"
            }
        ],
        companies_collection: store.collection("companies"),
        companies: [],
        unsubscribe: null
    }),
    created() {
        this.companies_collection.get().then(snapshot => {
            this.setData(snapshot);
            this.loading = false;
        });
        this.unsubscribe = this.companies_collection.onSnapshot(this.setData);
    },
    computed: {
        selected: {
            get() {
                return this.value;
            },
            set(new_value) {
                this.$emit("input", new_value);
            }
        }
    },
    methods: {
        setData(snapshot) {
            this.companies = snapshot.docs.map(doc => ({
                ...doc.data(),
                _id: doc.id
            }));
        }
    },
    destroyed() {
        this.unsubscribe();
    }
};
</script>

